import * as URL from '../../utility/url';
import * as Event from '../../events';
import {logout} from '../../services/authenticated';

class Homepage {
    constructor(pattern, data) {
        this.pattern = pattern;
        this.userData = data;
    }
    
    parseUrl(request) {
        return URL.parse('', request);
    }
    
    getServices() {
        this.parseUrl(/\/home\/.*/).then(res => {
            if (res == '/home/logout.html') {
                return this.logout();
            }
        })
        .catch(error => {});
    }
    
    logout() {
        const {uid} = this.userData;
        Event.loading(length => {
            if (length == 100) {
                return logout(uid);
            }
        });
    }
}

export default Homepage;