export const bind = () => {
    const Auth = new Authenticated({
        "administrator": "superuser",
        "users": "normal"
    });
    
    // Authenticate for administrator
    Auth.isAdmin((status,data) => {
        //generalDomain(status, /\/admin\/.*/, '/admin/index.html');
        githubPagesDomain(status, /\/portofolio\/admin\/.*/, '/portofolio/admin/index.html');
        
        if (status) {
            //let admin = new Administrator(/\/admin\/.*/, data);
            let admin = new Administrator(/\/portofolio\/admin\/.*/, data);
            return admin.getServices();
        }
    });
    
    // Authenticate for normal user
    Auth.isUsers((status, data) => {
        //generalDomain(status, /\/home\/.*/, '/home/index.html');
        githubPagesDomain(status, /\/portofolio\/home\/.*/, '/portofolio/home/index.html');
        
        if (status) {
            //let homepage = new Homepage(/\/home\/.*/, data);
            let homepage = new Homepage(/\/portofolio\/home\/.*/, data);
            return homepage.getServices();
        }
    });
};

export const generalDomain = (status,pattern,url) => {
    const Auth = new Authenticated({
        "administrator": "superuser",
        "users": "normal"
    });
    
    URL.parse('', pattern)
        .then(response => {
            return ((response && !status) ? Auth.redirectTo('../login.html') : false);
        })
        .catch(response => {
            return ((response && status) ? setTimeout(() => Auth.redirectTo(url),3000) : false);
        });
};

