export const setCloudService = (prepend, callback) => {
    if (!prepend) {
        return callback(false);
    }
    firebase.initializeApp(firebaseConfig);
    
    return callback(true);
};

export const isConnected = (callback) => {
    return firebase.database().ref('/.info/connected')
        .on('value', snap => {
            setTimeout(function() {
                if (snap.val() === true) {
                    callback(true);
                }
            }, 3000);
        });
};