/**
 * Github - Pages
 * 
 * Menentukan alamat dari github pages.
 * 
 * @author wadahkode <mvp.dedefilaras@gmail.com>
 * @since version 1.0.91
 */
export const defaultGhpages = () => 'https://wadahkode.github.io/portofolio';

export const getOrigin = address => {
    if (typeof address == 'undefined') {
        return {
            "origin": defaultGhpages().match(window.location.origin),
            "href": defaultGhpages() + window.location.pathname,
            "pathname": defaultGhpages().match('/portofolio')
        };
    } else {
        return {
            "href": address,
            "pathname": ""
        };
    }
};