import ArticleModel from '../model/article-model';
import * as URL from '../utility/url';
import * as GHPages from '../config/ghpages';
import {isNull} from 'lodash';

export const getArticle = id => {
    const model = new ArticleModel('article'),
        container = document.getElementById(id),
        pattern = /\/.*/g;
        
    URL.parse('', pattern).then(req => {
        if (req == GHPages.getOrigin().pathname) {
            return false;
        }
        
        if (!isNull(container)) {
            container.innerHTML = "";
            model.getArticle(response => {
                container.innerHTML += viewArticle(response);
            });
        }
    })
    .catch(error => {});
    
    const viewArticle = item => {
        return `
            <div class="uk-card uk-card-default">
                <figure class="uk-card-media-top uk-child-width-1-1">
                    <img src="${item.articlePhoto}" class="uk-height-medium" alt=""/>
                </figure>
                <div class="uk-card-body uk-padding-small">
                    <div class="uk-flex uk-flex-column">
                        <b class="uk-card-title uk-text-capitalize">${item.articleTitle}</b>
                        <b class="">@by ${item.author} pada ${item.created_at}</b>
                    </div>
                    <p class="uk-text-justify">${readMore(lineBreak(item.articleContent.toString(), '<br>'), item.articleUrl)}</p>
                </div>
            </div>
        `;
    };
};

export const lineBreak = (str, breakTag) => {
    str = (str.split("\\n").join(breakTag));
    return (str.split("@").join(breakTag + breakTag));
};

export const readMore = (str,link) => {
    return (str.substring(0,100) + '...&nbsp;<a href="' + link + '">baca selengkapnya</a>');
};